'use client'
import '../../public/css/bootstrap.css';
import Form from 'react-bootstrap/Form';

function Instructions(p) {

    const {text} = p;

  return (
      <Form.Group className="mb-1 w-50 text-wrap">
        <Form.Label size='lg' className='text-dark text-wrap'>{text}</Form.Label>
      </Form.Group>
  );
}

export default Instructions;