'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import FormDescription from "@/app/_components/formdescription";
import BasicTable from "@/app/_components/basictable";
import CSVLoader from "@/app/_components/csvloader";
import Instructions from "@/app/_components/instructions";

let pageDescription = ['Select a csv to import PTO notes']

let submitObject = {};

const defaultPeriod = 90;

//I HATE JS FOR MAKING ME DO THIS STUPID FORMATTING FOR DATES
//Thank you chat GPT for doing it for me

//Look into this https://medium.com/how-to-react/how-to-parse-or-read-csv-files-in-reactjs-81e8ee4870b0


const initState = {
    'Select File': {
        value: '',
        csv:''
    },
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    },
    'Fails':[{}],
    'Report Month':{
        value:''
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            return newState

            break
        }

        case 'load-CSV':{

            newState[action.label].csv = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }

        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

        case 'load-Report':{

            let students = action.report;

            newState['Fails'] = students;

            return newState

            break;
        }
        
    }
};

export default function RecordPTO(){


    const [state, dispatch] = useReducer(reducer,initState);

    submitObject = {
        report: state['Select File'].csv,
        month: state['Report Month'].value
    };

    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <FormTitle text='Create PTO Notes'></FormTitle>
            <FormDescription text={pageDescription}></FormDescription>
            <p>1. Run PTO report from ADP for a whole month. Include the following columns.</p>
            <p>2. Save the report as a csv file.</p>
            <p>3. Enter the first day of the month of the report</p>
            <p>4. Click Submit</p>
            <CSVLoader
                label='Select File'
                type = 'file'
                reminder =''
                placeholder = ''
                value = {state['Select File'].value}
                dispatch={dispatch}
            ></CSVLoader>
            <EntryBox
                label='Report Month'
                type = 'date'
                reminder =''
                placeholder = ''
                value = {state['Report Month'].value}
                dispatch={dispatch}
            ></EntryBox>
            <Submit 
                method="POST"
                endpoint="Litmos/AddPTO"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <BasicTable
                records={state['Fails']}
            >
            </BasicTable>
          </Form>
        </main>
    );
}