'use client'
import '../../public/css/bootstrap.css';
import Form from 'react-bootstrap/Form';
import Papa from 'papaparse';

function CSVLoader(p) {

    const {label,type='file',reminder, placeholder, value,dispatch,hidden=false} = p;

    const changeHandler = (event) =>{
        Papa.parse(event.target.files[0], {
            header: true,
            skipEmptyLines: true,
            complete: function (results) {
              console.log(results.data)
              dispatch({type:'load-CSV',value:results.data, label:label});
            }
        })
        dispatch({type:'update-Value',value:event.target.value, label:label});
        
    }


  return (
      <Form.Group hidden={hidden} className="mb-3" controlId={`form${label}`}>
        <Form.Label className='text-dark-emphasis badge'>{label}</Form.Label>
          <Form.Control type={type} accept=".csv" placeholder={placeholder} value={value} onChange={changeHandler} />
        <Form.Text className="text-danger badge">
          {reminder}
        </Form.Text>
      </Form.Group>
  );
}

export default CSVLoader;